<template>
  <div
    class="container"
    @mousedown="mouseOnDown"
    @mousemove="mouseMove"
    @mouseup="mouseUp"
  >
    <div class="top-space">
      <div class="link-to-back">
        <router-link to="/">Back to list</router-link>
      </div>
    </div>

    <div class="d-flex">
      <div class="mr-4">
        <div class="preview">
          <div
            class="preview-img d-flex flex-wrap"
            ref="preview"
            :class="`preview-img-${puzzle.grid.x}`"
          >
            <div v-for="(item, i) in puzzle.data" :key="i">
              <div
                class="preview-path"
                :class="
                  `option-box-${item.color[0] +
                    (item.color[1] ? '-' + item.color[1] : '')}`
                "
                :style="{ transform: 'rotate(' + item.rotate + 'deg)' }"
              ></div>
            </div>
          </div>

          <div class="preview-name">
            {{ this.currentPuzzleNumber }}. {{ puzzle.title }}
          </div>
        </div>
      </div>

      <div>
        <div
          class="matrix d-flex flex-wrap"
          ref="matrix"
          :class="`matrix-${puzzle.grid.x}`"
        >
          <div class="matrix-path" v-for="(item, i) in grid" :key="i">
            <i
              class="icon-rotate icon-rotate-left rotate-left"
              v-if="item.color != null && item.color[1]"
              @click="rotateLeft"
            ></i>
            <i
              class="icon-rotate icon-rotate-right rotate-right"
              v-if="item.color != null && item.color[1]"
              @click="rotateRight"
            ></i>
            <div
              v-if="item.color != null"
              class="option-box option-data"
              :class="
                `option-box-${item.color[0] +
                  (item.color[1] ? '-' + item.color[1] : '')}`
              "
              :data-color="item.color"
              :data-rotate="item.rotate"
              :data-index="i"
              :style="{ transform: 'rotate(' + item.rotate + 'deg)' }"
            ></div>
          </div>
        </div>
      </div>

      <div class="ml-auto">
        <div class="options-wrap">
          <div class="options d-flex flex-wrap" id="options" ref="options">
            <div
              class="options-item"
              v-for="item in optionsArray"
              :key="item.id"
            >
              <i
                class="icon-rotate icon-rotate-left rotate-left"
                v-if="item.color[1]"
                @click="rotateLeft"
              ></i>
              <i
                class="icon-rotate icon-rotate-right rotate-right"
                v-if="item.color[1]"
                @click="rotateRight"
              ></i>
              <div
                class="option-box option-data"
                :class="
                  `option-box-${item.color[0] +
                    (item.color[1] ? '-' + item.color[1] : '')}`
                "
                :data-color="item.color"
                :data-rotate="item.rotate"
              ></div>
            </div>
          </div>
        </div>

        <div class="options-transfer" ref="optionsTrans"></div>

        <div class="text-right">
          <button class="btn btn-success" @click="check">Check</button>
        </div>
      </div>
    </div>

    <div class="space-100"></div>
  </div>
</template>

<script>
const soundEffect = require("@/assets/fitting.mp3");

export default {
  name: "Constructor",
  data() {
    return {
      optionsArray: [],

      grid: [],

      //drag & drop
      draggable: false,
      boxOptionData: null,
      transfer: null,
      currentDroppable: null,
      targetMouseDownItem: null,
      shiftX: null,
      shiftY: null,

      //audio
      audioTap: soundEffect,
    };
  },

  computed: {
    puzzles() {
      return this.$store.state.puzzles
    },
    currentLevel() {
      return this.$route.params.level
    },
    currentPuzzleNumber() {
      return this.$route.params.puzzleNumber
    },
    puzzle() {
      return this.puzzles[this.currentLevel][this.currentPuzzleNumber - 1]
    }
  },

  methods: {
    check() {
      const alertText = JSON.stringify(this.puzzle.data) === JSON.stringify(this.grid) ? "Excellent!" : "Try one mor time :(";

      alert(alertText)
    },
    prepareVueOptions: (puzzle, optionsArray) => {
      // -- loop add options in options-array
      for (var i = 0; i < puzzle.data.length; i++) {
        const even = (el) =>
          JSON.stringify(el.color) === JSON.stringify(puzzle.data[i].color);

        if (!optionsArray.some(even)) {
          optionsArray.push(puzzle.data[i]);
        }
      }
    },

    moveAt(pageX, pageY, transfer) {
      transfer.style.left = pageX - this.shiftX + "px";
      transfer.style.top = pageY - this.shiftY + "px";
    },

    stopDrag() {
      this.draggable = false;
    },

    mouseMove($event) {
      if (this.draggable) {
        const transfer = this.$refs.optionsTrans;

        this.moveAt($event.pageX, $event.pageY, transfer);

        transfer.hidden = true;
        let elemBelow = document.elementFromPoint(
          $event.clientX,
          $event.clientY
        );
        transfer.hidden = false;

        if (!elemBelow) return;

        if (this.targetMouseDownItem.classList.contains("option-stuck")) {
          this.targetMouseDownItem.remove();
        }

        let droppableBelow = elemBelow.closest(".matrix-path");

        if (this.currentDroppable != droppableBelow) {
          if (this.currentDroppable) {
            // null when we were not over a droppable before this event
            this.leaveDroppable(this.currentDroppable);
          }

          this.currentDroppable = droppableBelow;

          if (this.currentDroppable) {
            // null if we're not coming over a droppable now
            // (maybe just left the droppable)
            this.enterDroppable(this.currentDroppable);
          }
        }
      }
    },

    mouseOnDown($event) {
      const transfer = this.$refs.optionsTrans;

      //const matrixPath = $event.target.closest(".matrix-path");

      if ($event.target.className.includes("option-box")) {
        this.draggable = true;

        let cssClasses = $event.target.classList;

        // check mouseDown go grid
        if ($event.target.parentNode.classList.contains("matrix-path")) {
          const wrapGrid = $event.target.closest(".matrix");

          const cellIndex = Array.from(wrapGrid.children).indexOf(
            $event.target.parentNode
          );

          this.grid[cellIndex].color = null;
          this.grid[cellIndex].rotate = null;
        }

        // get data path
        const getDataColor = JSON.stringify(
          $event.target.getAttribute("data-color")
        );
        const dataColor = JSON.parse(getDataColor).split(",");

        const getDataRotate = JSON.stringify(
          $event.target.getAttribute("data-rotate")
        );
        const dataRotate = JSON.parse(getDataRotate);

        // set data path
        this.boxOptionData = {
          color: dataColor,
          rotate: dataRotate,
        };

        transfer.hidden = false;

        this.targetMouseDownItem = $event.target;

        // mounting transfer view box
        transfer.classList = cssClasses;
        transfer.classList.remove("option-data");
        transfer.classList.add("options-transfer");

        transfer.style.transform = "rotate(" + dataRotate + "deg)";

        // -- position transfer box
        this.shiftX =
          $event.clientX - $event.target.getBoundingClientRect().left;
        this.shiftY =
          $event.clientY - $event.target.getBoundingClientRect().top;

        this.moveAt($event.pageX, $event.pageY, transfer);
      }
    },

    mouseUp($event) {
      if (this.draggable) {
        const transfer = this.$refs.optionsTrans;

        if (!this.currentDroppable) {
          const marker = document.querySelector("matrix-path-marker");
          if (marker) {
            marker.classList.remove("matrix-path-marker");
          }
        }

        transfer.removeAttribute("style");
        transfer.classList = "options-transfer";

        const matrixPath = $event.target.closest(".matrix-path");

        if (matrixPath) {
          // index el in matrix
          const indexPath = Array.from(matrixPath.parentNode.children).indexOf(
            matrixPath
          );

          this.grid[indexPath].color = this.boxOptionData.color;
          this.grid[indexPath].rotate = parseInt(this.boxOptionData.rotate);
        }

        // clear transfer data
        this.boxOptionData = null;

        this.targetMouseDownItem = null;
        this.currentDroppable = null;

        this.soundMouseUp();

        this.stopDrag();
      }
    },

    enterDroppable: (elem) => {
      elem.classList.add("matrix-path-marker");
    },
    leaveDroppable: (elem) => {
      elem.classList.remove("matrix-path-marker");
    },

    //rotate
    rotateRight(el) {
      const rotateArray = [0, 90, 180, 270];

      const optionBoxParent = el.target.parentNode;
      const optionBox = optionBoxParent.querySelector(".option-box");
      const rotate = Number(optionBox.getAttribute("data-rotate"));
      const index = Number(optionBox.getAttribute("data-index"));

      let setRotateVal = rotateArray[rotateArray.indexOf(rotate) + 1];

      if (rotateArray.indexOf(setRotateVal) === rotateArray.length - 1) {
        setRotateVal = rotateArray[0];
      }

      this.grid[index].rotate = setRotateVal;

      optionBox.setAttribute("data-rotate", setRotateVal);
      optionBox.style.transform = "rotate(" + setRotateVal + "deg)";
    },

    rotateLeft(el) {
      const rotateArray = [0, 90, 180, 270];

      const optionBoxParent = el.target.parentNode;
      const optionBox = optionBoxParent.querySelector(".option-box");
      const rotate = Number(optionBox.getAttribute("data-rotate"));
      const index = Number(optionBox.getAttribute("data-index"));

      let setRotateVal = rotateArray[rotateArray.indexOf(rotate) - 1];

      if (rotateArray.indexOf(setRotateVal) === -1) {
        setRotateVal = rotateArray[rotateArray.length - 1];
      }

      this.grid[index].rotate = setRotateVal;

      optionBox.setAttribute("data-rotate", setRotateVal);
      optionBox.style.transform = "rotate(" + setRotateVal + "deg)";
    },

    buildGrid() {
      const gridSize = this.puzzle.grid.x * this.puzzle.grid.y;

      let i = 0;
      while (i < gridSize) {
        this.grid.push({ color: null, rotate: null });
        i++;
      }
    },

    soundMouseUp() {
      var audio = new Audio();
      audio.src = this.audioTap;
      audio.autoplay = true; // Автоматически запускаем
    },
  },

  mounted() {
    this.buildGrid();

    this.prepareVueOptions(this.puzzle, this.optionsArray);

    window.addEventListener("mouseup", this.stopDrag);
  },
};
</script>
